import alt from '../alt';

class HomeActions {
  constructor() {
    this.generateActions(
      'getAllGoodsSuccess',
      'getAllGoodsFail',
      'addOneThisGood',
      'cutOneThisGood'
    );
  }

  getAllGoods() {
    $.ajax({ url: '/api/goods' })
      .done(data => {
        this.actions.getAllGoodsSuccess(data);
      })
      .fail(jqXhr => {
        this.actions.getAllGoodsFail(jqXhr.responseJSON.message);
      });
  }
}

export default alt.createActions(HomeActions);